body .breed-select.is-hidden, body .cat-info.is-hidden {
  display: none;
}

body .backdrop {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .416);
  justify-content: center;
  align-items: center;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

body .loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 2px solid #ff3d00;
  border-radius: 50%;
  margin: 5px;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

body .loader:after {
  content: "";
  box-sizing: border-box;
  width: 12px;
  height: 12px;
  border: 2px solid #fff;
  border-radius: 50%;
  position: absolute;
  top: 4px;
  left: 4px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

body .loader-text {
  font-size: 20px;
  font-weight: 600;
}

body.show-loader .backdrop {
  display: flex;
}

